#Contact h2{
    margin: 30px 0px;
    font-family:'Poppins', sans-serif;
    font-size: 30px;
    text-transform: uppercase;
    text-align: center;
}
.contactContainer{
    margin-top: 50px;
    display: grid;
    grid-template-columns: 2fr 1fr;
    column-gap: 71px;
}
.contactLeft{
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 15px;
    row-gap: 50px;
}
.contactLeft span{
    text-align: center;
}
.contactLeft h3{
    font-size: 22px;
}
.contactLeft p{
    font-size: 18px;
    margin-bottom: 10px;
}
.contactLeft :last-child{
    margin-bottom: 0px;
}
.contactRight h3{
    font-size: 22px;
    margin-bottom: 10px;
}
.contactRight .socialLinks{
    margin-top: 30px;
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    gap: 20px;
}

.socialLink a{    
    font-size: 18px;
    font-weight: 600;
    text-decoration: none;
    vertical-align: middle;
    padding: 5px 0px;
    transition: 0.3s ease-in-out;
}
.socialLink a:hover{
    border-left: 3px solid;
    padding: 5px 30px;
}

@media screen and (max-width:850px) {
    .contactContainer{
        grid-template-columns: 1fr;
        gap: 50px;
    }
    .contactRight {
        text-align: center;
    }
    .contactRight .socialLinks{
        align-items: center;
    }
}
